import * as React from 'react';

import {ClassNameBuilder} from '../../utils/ClassNameBuilder';
import {Icon} from '../Icon/Icon';

import Styles from './IconButton.module.scss';

interface Props {
    image: string;
    className?: string;
    title?: string;
    dataId?: string;
    onClick?: (event: React.MouseEvent) => void;
}

export const IconButton: React.FC<Props> = (props) => {
    const {className, image, dataId, onClick, title} = props;

    const containerClass = new ClassNameBuilder(Styles, 'container')
        .add(className)
        .toString();

    return (
        <div className={containerClass} onClick={onClick} data-id={dataId}>
            <div className={Styles.image}>
                <Icon src={image} title={title} />
            </div>
        </div>
    );
};
