import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {Provider} from 'react-redux';
import {Store} from 'redux';
import {History} from 'history';

import {Router, AppRoute} from '../components/Router/Router';
import {WindowProvider} from '../contexts/WindowContext';

/**
 * Render a component into a window.
 * @param Component Component to render.
 * @param window Target window.
 * @param store Provider store.
 * @param target DOM element to render to.
 * @param props Props to pass to the Component.
 */
export function renderComponentInWindow<T, S>(
    Component: (props: T) => React.ReactElement<T> | null,
    window: Window,
    store: Store<S>,
    target: Element,
    props: T
): void {
    ReactDOM.render(
        <Provider store={store}>
            <WindowProvider value={window}>
                <Component {...props} />
            </WindowProvider>
        </Provider>,
        target
    );
}

/**
 * Render a router and routes into a window.
 * @param routes Routes to display.
 * @param window Target window.
 * @param store Provider store.
 * @param history History state to use for the router.
 * @param target DOM element to render to.
 */
export function renderRouterInWindow<S>(
    routes: AppRoute[],
    window: Window,
    store: Store<S>,
    history: History,
    target: Element
): void {
    ReactDOM.render(
        <Provider store={store as unknown as Store<S>}>
            <WindowProvider value={window}>
                <Router routes={routes} history={history} />
            </WindowProvider>
        </Provider>,
        target
    );
}
