import * as React from 'react';

import {ClassNameBuilder} from '../../../utils/ClassNameBuilder';

import Styles from './Checkbox.module.scss';

interface Props {
    small?: boolean;
    disabled?: boolean;
    defaultChecked?: boolean;
    name?: string;
    onChange?: (event: React.ChangeEvent<HTMLInputElement>) => void;
    className?: string;
}

export const Checkbox: React.FC<Props> = (props) => {
    const {small, defaultChecked, disabled, name, onChange, className} = props;

    const containerClass = new ClassNameBuilder(Styles, 'checkbox')
        .add([className, !!className])
        .toString();

    const checkboxMockClass = new ClassNameBuilder(Styles, 'checkbox-mock')
        .add(['small', !!small])
        .add(['disabled', !!disabled])
        .toString();

    return (
        <label className={containerClass}>
            <input type="checkbox"
                defaultChecked={!!defaultChecked}
                disabled={!!disabled}
                name={name}
                onChange={(event) => onChange && onChange(event)}
            />
            <div className={checkboxMockClass} />
        </label>
    );
};
