import * as React from 'react';

import RunningIndicator from '../../assets/RunningIndicator.svg';
import {ClassNameBuilder} from '../../utils/ClassNameBuilder';
import {Icon} from '../Icon/Icon';

import * as Styles from './Cell.module.scss';

interface Props extends React.HTMLAttributes<HTMLElement> {
    text?: string;
    image?: React.ReactElement;
    className?: string;
    onClick?: (event: React.MouseEvent) => void;
    mouseEffects?: boolean;
    indicator?: boolean;
    children?: React.ReactNode;
}

export const Cell = React.forwardRef((props: Props, ref: React.RefObject<HTMLDivElement>) => {
    const {image, text, onClick, mouseEffects, indicator, className, children, ...rest} = props;
    const labelRef = React.createRef<HTMLSpanElement>();

    const containerClass = new ClassNameBuilder(Styles, 'cell')
        .add(className)
        .add(['mouse-effects', !!onClick || !!mouseEffects])
        .add('default-modifiers')
        .toString();

    React.useEffect(() => {
        const label = labelRef.current;

        // If the text is long enough to be truncated, add a tooltip with the full caption
        if (label && label.offsetWidth < label.scrollWidth) {
            label.setAttribute('title', text!);
        }
    }, [text]);

    return (
        <div className={containerClass} onClick={onClick} ref={ref} {...rest}>
            {indicator && <Icon src={RunningIndicator} className={Styles['running-indicator']} />}

            {image &&
                <div className={Styles['image']}>
                    {image}
                </div>
            }
            <span className={Styles['text']} ref={labelRef}>{text}</span>

            <div className={Styles['children-container']}>{children}</div>
        </div>
    );
});
