import * as React from 'react';

import * as Styles from './ImageAsBackground.module.scss';

interface Props {
    src: string;
    className?: string;
}

export const ImageAsBackground: React.FC<Props> = (props) => {
    const {src, className} = props;

    return (
        <div className={className}>
            <div className={Styles['image']} style={{backgroundImage: `url('${src}')`}}/>
        </div>
    );
};
