import * as React from 'react';

import {ListGroupProps, ListGroup, cleanChildren} from './ListGroup';
import {ListGroupItem} from './ListGroupItem';

export type SearchKeywordsCacheContext = Map<object, string[]> | undefined;

export const SearchKeywordsCache = React.createContext<SearchKeywordsCacheContext>(undefined);

const delimiter: RegExp = /[^a-zA-Z0-9]/g;

export interface SearchableListProps<T> extends ListGroupProps {
    search: string | undefined;
    searchTextsFactory: (entry: T) => string[];
    onListUpdate?: (results: T[]) => void;
}

type SearchableListType = <T>(props: SearchableListProps<T>) => React.ReactElement<SearchableListProps<T>>;

export const SearchableListGroup: SearchableListType = <T extends {}>(props: SearchableListProps<T>) => {
    const {search, className, searchTextsFactory, onListUpdate, ...rest} = props;
    const children = cleanChildren(props.children);
    const cache = React.useContext(SearchKeywordsCache);
    const searchTerms: string[] = search
        ? search
            .trim()
            .split(delimiter)
            .map((term) => term.toLowerCase())
            .filter((term) => term.length > 0)
        : [];

    // Separate displayed children, list items and their data.
    const filteredChildren: any[] = [];
    const filteredEntries: T[] = [];

    React.useMemo(() => {
        children.forEach((child) => {
            if (child.type !== ListGroupItem) {
                filteredChildren.push(child);
            } else if (entrySatisfiesSearch(child!.props.item, searchTerms, searchTextsFactory, cache)) {
                filteredEntries.push(child.props.item);
                filteredChildren.push(child);
            }
        });
    }, [searchTerms, children]);

    if (onListUpdate) {
        onListUpdate(filteredEntries);
    }

    return <ListGroup {...rest}>{filteredChildren}</ListGroup>;
};

function entrySatisfiesSearch<T>(entry: T, searchTerms: string[], searchTextsFactory: (entry: T) => string[], cache: SearchKeywordsCacheContext): boolean {
    const keywords: string[] = getKeywords(entry, searchTextsFactory, cache);

    return searchTerms.every((term: string) => {
        return keywords.some((keyword: string) => {
            return keyword.includes(term);
        });
    });
}

function getKeywords<T extends {}>(entry: T, searchTextsFactory: (entry: T) => string[], cache: Map<object, string[]> | undefined): string[] {
    const searchTexts = searchTextsFactory(entry);
    let keywords: string[] | undefined = (cache && cache.get(entry)) || undefined;

    if (!keywords) {
        keywords = [];

        for (const searchText of searchTexts) {
            keywords.push(...searchText?.split(delimiter).map((term) => term.toLocaleLowerCase()));
        }

        // Remove empty and duplicate entries
        keywords = keywords.filter((term, index, array) => {
            return term.length > 0 && array.indexOf(term) === index;
        });

        if (cache) {
            cache.set(entry, keywords);
        }
    }

    return keywords;
}
