/**
 * Polyfill for :focus-visible pseudo-element. Currently locked behind Chrome experimental flag.
 * Allows for keyboard-only focused event instead of :focus which captures both keyboard and mouse.
 */
import 'focus-visible';
import * as React from 'react';
import './focus-visible-polyfill.scss';

import {ClassNameBuilder} from '../../../utils/ClassNameBuilder';

import Styles from './Button.module.scss';

enum ButtonColor {
    primary,
    secondary,
    success,
    danger,
    warning,
    text
}

interface Props {
    onClick?: (event: React.MouseEvent) => void;
    disabled?: boolean;
    className?: string;
    block?: boolean;
    dataId?: string;
    color?: (keyof typeof ButtonColor) | ButtonColor;
}

export const Button: React.FC<Props> = (props) => {
    const {color = ButtonColor.primary, block, disabled, className, onClick, dataId, children} = props;
    const containerClass = new ClassNameBuilder(Styles)
        .add(Styles.button)
        .add(color as string)
        .add([Styles.disabled, disabled])
        .add([Styles.block, block])
        .add(className)
        .toString();

    return (
        <button
            className={containerClass}
            onClick={onClick}
            disabled={disabled}
            data-id={dataId}
        >
            {children}
        </button>
    );
};
