import * as React from 'react';

import CrossIcon from '../../assets/Cross.svg';
import {Cell} from '../Cell/Cell';
import {ClassNameBuilder} from '../../utils/ClassNameBuilder';
import {Icon} from '../Icon/Icon';
import {IconButton} from '../IconButton/IconButton';

import * as Styles from './Header.module.scss';

interface Props {
    text?: string;
    image?: string;
    className?: string;
    onClose?: (event: React.MouseEvent) => void;
}

export const Header: React.FC<Props> = (props) => {
    const {text, image, className, onClose, children} = props;

    const containerClass = new ClassNameBuilder(Styles, 'header')
        .add(className)
        .toString();

    return (
        <Cell className={containerClass} text={text} image={image ? <Icon src={image} /> : undefined}>
            {children}
            <IconButton image={CrossIcon} onClick={onClose} dataId={'header-close-button'}/>
        </Cell>
    );
};
